<template>
    <div class="page_">
        <img class="bg_" src="https://cdn.youlianyc.com/image/static/41d3e38aa72a2aad19ef003c579a3fd7feffa39d.jpg">
        <div class="content_">
            <div class="title_">欢迎加入</div>
            <div class="class_num_">换换官方福利①班</div>
            <div class="tips_">长按识别下方二维码进群</div>
            <div class="img_wrap_"><img :src="qrcodeUrl"></div>
            <div class="hint_">
                <p>二维码仅支持页面内容识别</p>
                不支持截图转发
            </div>
        </div>
    </div>
</template>
<style lang="scss" scoped>
    .page_ {
        text-align: center;
        position: relative;
        width: 100%;
        height: 100vh;
        background-image: url('');
        background:linear-gradient(180deg,rgba(253,94,2,1) 0%,rgba(255,236,49,1) 100%);
        background-size: 100% auto;

        .bg_ {
            width: 100%;
