</style>

<script>
import api from '@/utils/api'
export default {
    data () {
        return {
            qrcodeUrl: ''
        }
    },
    mounted () {
        console.log(this.$route.query.code)
        const code = this.$route.query.code
        let params = {}
        if (localStorage.getItem('open_id')) {
            params.open_id = localStorage.getItem('open_id')
            this.getWeixinQrcode(params)
        } else if (code) {
            params.code = code
            this.getWeixinQrcode(params)
        } else {
            this.jumpToAuthroize(params)
        }
    },
    methods: {
        jumpToAuthroize () {
            const redirect_uri = encodeURIComponent(`${location.origin}/m/weixin_qrcode`)
            window.location.href = `https://open.weixin.qq.com/connect/oauth2/authorize?appid=wxd348604f00b38a66&redirect_uri=${redirect_uri}&response_type=code&scope=snsapi_userinfo&state=1&connect_redirect=1#wechat_redirect`
        },
        getWeixinQrcode (params) {
            api.getWeixinQrcode(params).then(res => res.data).then(data => {
                console.log(data, '1')
                if (data.status === 'ok') {
                    const {qr_code, end_time, open_id} = data.payload
                    this.qrcodeUrl = qr_code
                    localStorage.setItem('open_id', open_id)
                } else {
                    // this.jumpToAuthroize()
                    localStorage.setItem('open_id', '')
                    alert(data.message)
                }
            })
        }
    }
}
</script>



